/* login.module.css */
.loadingOverlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgb(255, 255, 255);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 999;
}

.loadingSpinner {
  border: 8px solid #f3f3f3;
  border-top: 8px solid #3498db;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}



.loginContainer {
    background-color: #cde9fd;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
  }
  
  .squareLeft {
    width: 280px;
    height: 545px;
    background-color: #ffffff;
    border-radius: 18px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    overflow: hidden; /* Обрезаем изображение по размерам блока */
  }
  
  .LeftImg {
    width: 100%;
    height: 100%;
    border-radius: 18px;
  }
  
  
  .squareRight {
    height: 480px;
    width: 350px;
    margin-left: 30px;
    background-color: #ffffff;
    border-radius: 18px;
    padding: 40px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  }
  
  .squareText {
    text-align: center;
  }
  
  .title {
    font-size: 40px;
    font-weight: 800;
  }
  
  .desc {
    margin-top: 5px;
    font-size: 16px;
    font-weight: 600;
  }
  
  .form {
    text-align: center;
  }
  
  .formInput {
    width: 100%;
    box-sizing: border-box; /* Добавлено */
    font-family: 'Nunito', sans-serif;
    border: 1px rgb(205, 210, 219) solid;
    border-radius: 11px;
    background: none;
    padding: 10px;
    margin-top: 13px;
    font-size: 17px;
    font-weight: 600;
  }
  
  .formInput::placeholder {
    color: rgb(107, 107, 107);
    font-size: 16px;
    font-weight: 600;
    letter-spacing: 0.5px;
  }
  
  .formBtn {}
  
  .btn {
    font-family: 'Nunito', sans-serif;
    border: 0;
    margin-top: 20px;
    font-size: 17px;
    font-weight: 600;
    background-color: #3498db;
    color: white;
    border-radius: 11px;
    padding: 14px 60px;
    width: 100%;
    transition: background-color .3s ease-in-out;
  }
  
  .btn:hover {
    background-color: #2777ac;
  }
  
  .bottomDesc { 
    margin-top: 30px;
    border-top: 2px rgb(230, 230, 230) solid;
    display: flex;
    gap: 15px;
    padding: 20px 0 0 0;
  }
  .descText { 
    font-size: 16px;
    font-weight: 600;
  }
  .descLink { 
    font-size: 17px;
    font-weight: 700;
    color: #2f8dcc;
    text-decoration: none;
  }